import React, {Component} from 'react';

import logo from './logo.png';
import './App.css';

import 'semantic-ui-css/semantic.min.css';

// import { Input } from 'semantic-ui-react';

import moment from 'moment';

import {
    isBrowser,
    isMobile
} from "react-device-detect";

import {
    Card,
    Icon,
    Image,
    Input,
    Container,
    Accordion
} from 'semantic-ui-react'


const algoliasearch = require("algoliasearch");
const client = algoliasearch("AB7G20A8HU", "b66828a8b34cae4dcd4d56bd164b0a9f");
const index = client.initIndex("prod_fa_products_azure_searchable");

export default class App extends Component {

    constructor(props) {
        super(props);
        this.state = {
            searchQuery: '',
            searchResults: [],
            loading: false
        }
    }

    inputChange = async (event) => {
        let currentSearch = event.target.value;

        this.setState({
            searchQuery: currentSearch
        });

        setTimeout(() => {
            if (currentSearch === this.state.searchQuery) {
                this.search(currentSearch);
            }
        }, 750)


    }

    search = async (currentSearch) => {

        this.setState({
            loading: true
        });

        index
            .search(this.state.searchQuery, {
                hitsPerPage: 300
            })
            .then(async ({ hits }) => {
                if (currentSearch === this.state.searchQuery) {
                    this.setState({
                        searchResults: hits,
                        loading: false
                    })
                } else {
                    return;
                }
            })
            .catch(err => {
                console.log(err);
            });
    }

    render() {

        console.log(this.state)

        return (
            <div className="App">
                <Container>
                    <div className="App-container">

                        <div style={{ padding: '20px 0 0 0' }}>
                            <Image src={logo} size='medium' centered />
                        </div>

                        <Input
                            label={{ basic: true, content: `${this.state.searchResults.length} found` }}
                            labelPosition='left'
                            value={this.state.searchQuery}
                            loading={this.state.loading}
                            onChange={this.inputChange}
                            placeholder='Enter search query...'
                            fluid
                            style={{
                                padding: '30px 0'
                            }}
                        />

                        <div style={{
                            padding: '20px 0'
                        }}>
                            <Card.Group itemsPerRow={(isMobile) ? 2 : 4}>
                                {
                                    (this.state.searchResults.length !== 0) ? (
                                        this.state.searchResults.map((hit) => {
                                            console.log(hit)
                                            return (
                                                <Card>
                                                    <Image src={hit.image} wrapped ui={false} />
                                                    <Card.Content>
                                                        <Card.Header style={{
                                                            textOverflow: 'ellipsis',
                                                            overflow: 'hidden',
                                                            whiteSpace: 'pre'
                                                        }}>{hit.product}</Card.Header>
                                                        <Card.Meta>
                                                            <span className='date'>{hit.id}</span>
                                                        </Card.Meta>
                                                        {/*<Card.Description>*/}
                                                        {/*    <Accordion panels={[{*/}
                                                        {/*        key: 'description',*/}
                                                        {/*        title: 'Description',*/}
                                                        {/*        content: hit.description,*/}
                                                        {/*    }]} />*/}
                                                        {/*</Card.Description>*/}
                                                    </Card.Content>
                                                    <Card.Content extra>
                                                        <a>
                                                            <Icon name='time' />
                                                            {moment(hit['added-on'].date).fromNow()}
                                                            {}
                                                        </a>
                                                    </Card.Content>
                                                </Card>
                                            )
                                        })
                                    ) : null
                                }
                            </Card.Group>
                        </div>

                    </div>
                </Container>
            </div>
        );
    }

};